<template>
  <fieldset class="form--block">
    <legend class="sub-header checkout--sub-header">Термін дії полісу</legend>
    <div class="form--row checkout--row">
      <div class="checkout--sub-row">
        <div class="checkout--wrap">
          <label for="checkout-from" class="sub2-header checkout--sub2-header checkout--from">з</label>
          <input id="checkout-from"
                 type="date"
                 :min="dayFrom"
                 name="birth-date"
                 v-model="dayFrom"
                 @change="changeTermFrom($event.target.value)"
                 class="form--input checkout--input checkout--input__date"
                 required>

          <label for="checkout-to" class="sub2-header checkout--sub2-header checkout--from">по</label>
          <input id="checkout-to"
                 type="date"
                 disabled
                 name="birth-date"
                 :value="lastDay"
                 class="form--input checkout--input checkout--input__date"
                 required>
        </div>

      </div>
    </div>
  </fieldset>
</template>

<script>
  import {mapGetters} from "vuex";
  import {mapActions} from "vuex";
  import {TheMask} from 'vue-the-mask';
  import vSelect from "vue-select";

  export default {
    data() {
      return {
        dayFrom: new Date().toISOString().split("T")[0],
      }
    },

    computed: {
      ...mapGetters("period",{
        period: "period"
      }),

      lastDay() {
        const selectedDate = new Date(this.dayFrom);
        selectedDate.setMonth(selectedDate.getMonth() + this.period);
        selectedDate.setDate(selectedDate.getDate() - 1);
        const lastDay = new Date(selectedDate).toISOString().split("T")[0];
        this.changeTermTo(lastDay);

        return lastDay
      }
    },

    methods: {
      ...mapActions("term", {
        changeTermFrom: "changeTermFrom",
        changeTermTo: "changeTermTo",
      })
    },

    components: {
      TheMask,
      vSelect
    }
  }
</script>


<style>
  .checkout--from {
    display: inline-block;
    margin-right: 7px;
    margin-left: 7px;
  }
</style>




