<template>
  <fieldset class="form--block">
    <legend class="sub-header checkout--sub-header">Транспортний засіб</legend>
    <div class="form--row checkout--row">
      <div class="checkout--sub-row">
        <div class="checkout--wrap">
          <label for="auto-number" class="sub2-header checkout--sub2-header">Номер автомобіля</label>
          <the-mask :value="clientData.autoNumber"
                    @input="changeAutoNumberValue($event)"
                    :mask="specialNumber ? 'NNNNNNNN' : 'FF####FF'"
                    :tokens="autoNumberToken"
                    id="auto-number"
                    type="text"
                    class="form--input checkout--input checkout--input__auto-number"
                    placeholder="АА1234АА"
                    name="auto-number"
                    required
                    :pattern="specialNumber ? false : '[A-Za-zА-Яа-яіІєЄїЇґҐ]{2}[\\d]{4}[A-Za-zА-Яа-яіІєЄїЇґҐ]{2}'"
                    oninvalid="setCustomValidity('Номер автомобіля повинен складатись з букв та цифр')"
                    onchange="try{setCustomValidity('')}catch(e){}">
          </the-mask>

          <div>
            <label for="special-number" class="form--label-cont checkout--no__number">
              Нестандартний номер
              <input v-model="specialNumber"
                     id="special-number"
                     name="special-number"
                     type="checkbox"
                     class="form--check form--radio">
              <span class="form--checkmark"></span>
            </label>
          </div>
        </div>
      </div>

      <div class="checkout--sub-row">
        <div class="checkout--wrap">
          <label for="mark" class="sub2-header checkout--sub2-header">Марка</label>
          <input :value="clientData.mark"
                 @input="changeMarkValue($event.target.value)"
                 id="mark"
                 class="form--input checkout--input"
                 placeholder="Hyundai"
                 name="mark"
                 required>
        </div>
        <div class="checkout--wrap">
          <label for="model" class="sub2-header checkout--sub2-header">Модель</label>
          <input :value="clientData.model"
                 @input="changeModelValue($event.target.value)"
                 id="model"
                 class="form--input checkout--input"
                 placeholder="Tucson"
                 name="model"
                 required>
        </div>
        <div class="checkout--wrap">
          <label for="chassis" class="sub2-header checkout--sub2-header">Номер шасі</label>
          <the-mask :value="clientData.chassis"
                    @input="changeChassisValue($event)"
                    mask="FFFFFFFFFFFFFFFFF"
                    :tokens="chassisTokens"
                    id="chassis"
                    type="text"
                    name="chassis"
                    class="form--input checkout--input checkout--input__chassis"
                    placeholder="1234ABCD5678EFG90"
                    required
                    pattern="[a-zA-Z0-9-]{4,17}"
                    oninvalid="setCustomValidity('Номер шасі повинен складатись максимум із 17 символів (латинских букв і цифр)')"
                    onchange="try{setCustomValidity('')}catch(e){}">
          </the-mask>
        </div>
        <div class="checkout--wrap">
          <label for="transport-year" class="sub2-header checkout--sub2-header">Рік випуску</label>
          <input :value="clientData.autoYear"
                 @input="changeAutoYearValue($event.target.value)"
                 id="transport-year"
                 type="number"
                 min="1920"
                 :max="new Date().getFullYear()"
                 name="transport-year"
                 class="form--input checkout--input checkout--input__adress-number"
                 placeholder="2008"
                 required>
        </div>
      </div>
    </div>
  </fieldset>
</template>

<script>
  import {mapGetters} from "vuex";
  import {mapActions} from "vuex";
  import {TheMask} from 'vue-the-mask';

  export default {
    data() {
      return {
        specialNumber: false,
        chassisTokens: {
          F: {
            pattern: /[a-zA-Z0-9-]+/i,
            transform: v => v.toLocaleUpperCase()
          }
        },
        autoNumberToken: {
          N: {
            pattern: /./,
            transform: v => v.toLocaleUpperCase()
          },
          F: {
            pattern: /[a-zа-яёієїґ]+$/i,
            transform: v => v.toLocaleUpperCase()
          },
          '#': {
            pattern: /\d/
          },
        }
      }
    },

    computed: {
      ...mapGetters([
        "clientData"
      ]),
    },

    methods: {
      ...mapActions("vehicle", {
        changeMarkValue: "changeMarkValue",
        changeModelValue: "changeModelValue",
        changeChassisValue: "changeChassisValue",
        changeAutoNumberValue: "changeAutoNumberValue",
        changeAutoYearValue: "changeAutoYearValue"
      }),
    },

    components: {
      TheMask
    }
  }
</script>





